<template>
    <v-app>
        <v-app-bar
                app
                color="primary"
                dark
        >
            <v-toolbar-title class="ma-2">Sandwich</v-toolbar-title>
            <v-spacer></v-spacer>
            <v-menu bottom left>
                <template v-slot:activator="{ on }">
                    <v-btn class="ma-2" dark icon v-on="on">
                        <v-icon>mdi-cloud-download</v-icon>
                    </v-btn>
                </template>

                <v-list max-height="500" class="overflow-y-auto">
                    <v-subheader>Download Speed: {{ downloadSpeed }}</v-subheader>
                    <v-divider></v-divider>
                    <v-list-item
                            v-for="(item, i) in items"
                            :key="i"
                    >
                        <v-list-item-icon>
                            <v-progress-circular
                                    :size="32"
                                    :value="item.p"
                                    :width="2"
                                    color="light-blue"
                            >{{ item.p }}</v-progress-circular>
                        </v-list-item-icon>
                        <v-list-item-title>{{ item.title }}</v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>
        </v-app-bar>

        <v-content>
            <GameTable/>
        </v-content>
    </v-app>
</template>

<script>
    import GameTable from './components/GameTable';

    export default {
        name: 'App',
        components: {
            GameTable: GameTable,
        },
        data: () => ({
            darkmode: true,
            aria2: require("aria2"),
            ls: require('local-storage'),
            path: require('path'),
            prettyBytes: require('pretty-bytes'),

            items: [],
            downloadSpeed: ''
        }),
        methods: {
            load_rpc_task (tasks) {
                let ret = [];
                for (let obj of tasks) {
                    let task = {};
                    task.p = obj.totalLength === '0' ? 0 : parseInt(obj.completedLength / obj.totalLength * 100);
                    if (obj.totalLength === '0') {
                        task.title = 'waiting: ' + obj.gid;
                    } else {
                        task.title = this.path.basename(obj.files[0].path);
                    }
                    ret.push(task);
                }
                return ret;
            },
            async load_aria2() {
                let host = this.ls.get('rpc_host');
                let port = this.ls.get('rpc_port');
                if (host && port) {
                    try {
                        const aria2 = new this.aria2({host: host, port: port});
                        let new_list = [];

                        let tasks = await aria2.call('tellActive');
                        new_list = new_list.concat(await this.load_rpc_task(tasks));

                        tasks = await aria2.call('tellWaiting', 0, 100);
                        new_list = new_list.concat(await this.load_rpc_task(tasks));

                        tasks = await aria2.call('tellStopped', 0, 100);
                        new_list = new_list.concat(await this.load_rpc_task(tasks));

                        let global = await aria2.call('getGlobalStat');
                        this.downloadSpeed = this.prettyBytes(parseInt(global.downloadSpeed)) + '/S';
                        this.items = new_list;
                    } catch (e) { null; }
                }
                setTimeout(this.load_aria2, 3000);
            }
        },
        created () {
            setTimeout(this.load_aria2, 3000);
            // this.$vuetify.theme.dark = true;
        },
    };
</script>
