<template>
    <v-card>
        <v-card-title>
            Games Count: {{ count }}
            <v-spacer></v-spacer>
            <v-text-field
                    v-model="search"
                    append-icon="mdi-magnify"
                    label="Search"
                    single-line
                    hide-details
            ></v-text-field>
        </v-card-title>
        <v-data-table
                :headers="headers"
                :items="games"
                :search="search"

                :loading="loading"
                loading-text="Loading... Please wait"

                :sort-by.sync="sortBy"
                :sort-desc.sync="sortDesc"

                @click:row="handleClick"
        >

            <template v-slot:item.icon="{ item }">
                <v-layout justify-center><img :src=item.icon /></v-layout>
            </template>

        </v-data-table>


        <v-dialog v-model="download" fullscreen hide-overlay transition="dialog-bottom-transition">
            <v-card>
                <v-toolbar dark color="primary">
                    <v-btn icon dark @click="download = false">
                        <v-icon>mdi-close</v-icon>
                    </v-btn>
                    <v-toolbar-title>{{ game_name }}</v-toolbar-title>
                </v-toolbar>
                <v-tabs v-model="tab" :centered="true">
                    <v-tab v-for="(ft, index) in download_tabs" :key="index">{{ ft }}</v-tab>
                </v-tabs>
                <v-tabs-items v-model="tab">
                    <v-tab-item v-for="(value, key, index) in download_content" :key="index">
                        <v-simple-table>
                            <tbody>
                            <tr v-for="(obj, j) in download_content[key]" :key="j">
                                <td>
                                    <v-checkbox v-model="obj.checked"></v-checkbox>
                                </td>
                                <td>{{ obj.size }}</td>
                                <td>{{ obj.modifiedTime }}</td>
                                <td>
                                    <v-chip
                                            class="ma-2"
                                            :color="obj.latest ? 'orange' : obj.color"
                                            small
                                    >
                                        {{ obj.cate }}
                                    </v-chip>
                                </td>
                                <td>
                                    <a :href="obj.url" target="view_window">{{ obj.name }}</a>
                                </td>
                            </tr>
                            </tbody>
                        </v-simple-table>
                    </v-tab-item>
                </v-tabs-items>
                <div class="text-center">
                    <v-btn class="ma-2" @click="_export()" color="primary">Export Aria2 RPC</v-btn>
                </div>
                <br />
            </v-card>
        </v-dialog>


        <v-dialog
                v-model="searching"
                hide-overlay
                persistent
                width="300"
        >
            <v-card
                    color="primary"
                    dark
            >
                <v-card-text>
                    <br /><p class="text-md-center subtitle-1">Searching</p>
                    <v-progress-linear
                            indeterminate
                            color="white"
                            class="mb-0"
                    ></v-progress-linear>
                </v-card-text>
            </v-card>
        </v-dialog>

        <v-snackbar v-model="snackbar" :timeout="3000">
            {{ tips }}
        </v-snackbar>

        <InputsDialog
                ref="dlg_aria2"
                title="Export to aria2 RPC"
                text="Please run (aria2c --enable-rpc --rpc-listen-all=true --rpc-allow-origin-all --max-tries=20 --retry-wait=10) on your local or NAS first."
                :inputs_labels="['host', 'port', 'download_dir']"
                :okfunc="_export"
        ></InputsDialog>

    </v-card>
</template>

<script>
    import InputsDialog from "./InputsDialog";

    export default {
        components: { InputsDialog },
        data () {
            return {
                aria2: require("aria2"),
                axios: require('axios'),
                prettyBytes: require('pretty-bytes'),
                qs: require('qs'),
                dateFormat: require('dateformat'),
                ls: require('local-storage'),

                sortBy: 'user_rating',
                sortDesc: true,
                count: 0,
                loading: true,
                search: '',
                headers: [
                    { text: '#', value: 'icon', sortable: false },
                    { text: 'Name', value: 'name' },
                    { text: 'Publisher', value: 'publisher' },
                    { text: 'Release', value: 'release_date' },
                    { text: 'Size', value: 'size' },
                    { text: 'Hours', value: 'playtime' },
                    { text: 'Rating', value: 'user_rating' }
                ],
                games: [],

                file_types: ['NSZ', 'NSP', 'XCI'],
                download_tabs: [],
                download_content: {},
                download: false,
                searching: false,
                game_name: "",
                tab: null,
                snackbar: false,
                tips: '',

                rpc_host: null,
                rpc_port: 0,
                rpc_download_dir: null,

                // host_url: 'https://ns.996247.xyz',
                host_url: '',
            }
        },
        created() {
            let cache = parseInt(new Date().getTime() / 1000000) * 1000;
            this.axios.get('https://tinfoil.media/Title/ApiJson/?_=' + cache)
                .then((response) => {
                    for (let g of response.data.data) {
                        let iconr = /https:\/\/.*?(?=\))/.exec(g.icon);
                        this.games.push({
                            id: g.id,
                            publisher: g.publisher,
                            release_date: g.release_date || '2000-01-01',
                            size: g.size,
                            playtime: g.playtime || 0,
                            user_rating: g.user_rating,
                            name: />(.*?)(?=<)/.exec(g.name.replace('\n', ''))[1],
                            icon: iconr ? iconr[0] : ''
                        });
                    }
                    this.count = this.games.length;
                    this.loading = false;
                });
            if (this.host_url === '') {
                let href = window.location.href;
                this.host_url = href.substring(0, href.length - 1);
            }
        },
        methods: {
            handleClick (value) {
                this.searching = true;
                this.axios.post(this.host_url + '/search', this.qs.stringify({ 'q': value.id }))
                    .then((response) => {
                        this.searching = false;
                        this.game_name = value.name;
                        this.download = true;
                        this.download_tabs = [];
                        this.download_content = {};

                        let files = response.data.data.files;
                        let ids = {base: value.id, update: this.update_id(value.id), dlc: this.dlc_id(value.id)};
                        console.log(files);
                        for (let type of this.file_types) {
                            for (let cate in ids) {
                                let latest = true;
                                for (let file of files) {
                                    if (file.name.toLowerCase().indexOf(type.toLowerCase())  > 0
                                        && file.name.toLowerCase().indexOf(ids[cate].toLowerCase()) > 0) {
                                        if (this.download_content[type] === undefined) {
                                            this.download_content[type] = [];
                                        }
                                        if (!this.download_tabs.includes(type)) {
                                            this.download_tabs.push(type);
                                        }
                                        file.cate = cate;
                                        file.color = {'base': 'primary', 'update': 'secondary', 'dlc': ''}[cate];
                                        file.size = this.prettyBytes(parseInt(file.size));
                                        file.modifiedTime = this.dateFormat(new Date(file.modifiedTime), 'yyyy-mm-dd');
                                        file.checked = false;
                                        file.url = `${ this.host_url }/get_file?${ this.qs.stringify({
                                            'name': file.name,
                                            'parent': file.parents[0]
                                        }) }` ;
                                        file.latest = cate === 'update' ? latest : false;
                                        this.download_content[type].push(file);
                                        latest = false;
                                    }
                                }
                            }
                        }
                        console.log(this.download_content);
                    });
            },
            update_id(id) {
                return id.substring(0, id.length-3) + '800';
            },
            dlc_id(id) {
                return id.substring(0, id.length-4) + String.fromCharCode(id.charCodeAt(12)+1);
            },
            async _export(v) {
                if (v) {
                    this.rpc_host = v[0];
                    this.rpc_port = parseInt(v[1]);
                    this.rpc_download_dir = v[2];
                    this.ls.set('rpc_host', v[0]);
                    this.ls.set('rpc_port', v[1]);
                    this.ls.set('rpc_download_dir', v[2]);
                }
                if (this.rpc_download_dir === null) {
                    const dlg = this.$refs['dlg_aria2'];
                    this.$set(dlg.inputs_vals, 0, this.ls.get('rpc_host') ? this.ls.get('rpc_host') : 'localhost');
                    this.$set(dlg.inputs_vals, 1, this.ls.get('rpc_port') ? this.ls.get('rpc_port') : '6800');
                    this.$set(dlg.inputs_vals, 2, this.ls.get('rpc_download_dir') ? this.ls.get('rpc_download_dir') : '/');
                    dlg.show();
                    return;
                }
                if (this.rpc_download_dir) {
                    const aria2 = new this.aria2({host: this.rpc_host, port: this.rpc_port});
                    let success_id = [];
                    for (let key in this.download_content) {
                        for (let obj of this.download_content[key]) {
                            if (obj.checked) {
                                try {
                                    const [guid] = await aria2.call("addUri", [obj.url], {dir: this.rpc_download_dir});
                                    success_id.push(guid);
                                    console.log(guid);
                                } catch (e) {
                                    this.tips = 'connect RPC failed!';
                                    this.snackbar = true;
                                }
                            }
                        }
                    }
                    if (success_id) {
                        this.tips = `add tasks success, tasks id = [${ success_id.join(', ') }]`;
                        this.snackbar = true;
                    }
                }
            }
        }
    }
</script>
