<template>
    <v-dialog v-model="dlg" persistent max-width="290">
        <v-card>
            <v-card-title class="headline">{{title || '确认操作'}}</v-card-title>
            <v-card-text>
                <template v-show="d_text">{{d_text}}</template>
                <v-text-field v-for="(label,idx) in inputs_labels" :label="label" :key="idx"
                              :value="inputs_vals[idx]"
                              @change="$set(inputs_vals,idx,$event)"
                              required></v-text-field>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="green darken-1" text @click="clickOk()"
                       :disabled="dlg_btns_disabled">Confirm
                </v-btn>
                <v-btn color="grey darken-1" text
                       @click="close()"
                       :disabled="dlg_btns_disabled">Cancel
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<script>
    export default {
        name: "InputsDialog",
        props: ['okfunc', 'okparams', 'okclose', 'title', 'text', 'inputs_labels'],
        data() {
            return {
                d_text: this.text,
                dlg: false,
                dlg_btns_disabled: false,
                inputs_vals: []
            }
        },
        methods: {
            open() {
                (!this.dlg) && (this.dlg = true)
            },
            show() {
                this.open();
            },
            close() {
                this.dlg && (this.dlg = false)
                const me = this;
                this.inputs_labels.forEach((v, idx) => me.$set(me.inputs_vals, idx, ''));
            },
            async clickOk() {
                this.dlg_btns_disabled = true;
                if (this.okfunc) {
                    await this.okfunc(this.inputs_vals, ...(this.okparams || []));
                }
                this.dlg_btns_disabled = false;
                if (this.okclose !== false) {
                    this.close();
                }
            },
        }
    }
</script>
